
#app {
	font-family: "Avenir", Helvetica, Arial, sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	text-align: center;
	color: #2c3e50;
	margin-top: 60px;
}
.cursor-pointer {
	cursor: pointer;
}
.text-danger {
	color: red;
}
.text-success {
	color: #09f;
}
.task--done {
	text-decoration: line-through;
	color: #999;
}
.todo-container--revert-icon {
	cursor: pointer;
	color: #09f;
}
.todo-container--delete-icon {
	cursor: pointer;
	color: red;
}
.todo--checkbox {
	width: 17px;
	margin-right: 10px;
}
#new-task--todo {
	padding-right: 75px !important;
}




















































































































































































































































































































































































































































































